.list__title {
  font-size: 18px;
  color: rgb(94, 28, 216);
  text-align: center;
  padding: 0 10px;
  @media screen and (min-width: 320px) {
    font-size: 36px;
  }
  @media screen and (min-width: 768px) {
    font-size: 46px;
  }
}

.film_list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 25px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 50px;
    justify-content: center;
  }
}

.film_list a {
  /* border: 2px solid tomato; */
}

.list__item_title {
  display: block;
  margin: 0 0 10px 0;
}

.list__item {
  list-style: none;
  font-size: 22px;
  font-weight: 600;
  text-decoration: underline;
  color: rgb(170, 163, 163);
  width: 280px;
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 250ms;
  text-align: center;
   @media screen and (min-width: 768px) {
    width: 320px;
    height: 440px;
  }
}

.list__item img {
  @media screen and (min-width: 768px) {
    width: 280px;
    height: 400px;
  }
}

.list__item:hover {
  transform: scale(1.05);
  color: rgb(13, 163, 53);
}
